.delete-btn {
  float: right;
  color: #e74944;
}

.col-md-4, .col-sm-4{
    margin-top: 30px;
}
.colDarkBg{
    background-color: black;
    color: white;
    padding: 20px;
}
.colLightBorder{
    border: 1px solid gray;
    box-shadow: 2px 5px 10px 6px;
}
.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

.postCategory{
    width: 100%;
}
.footerCustom{
    background-color: black;
    padding: 10px 0;
    margin-top: 50px;
    text-align: center;
    color: white;
    font-weight: bolder;
}
.moreBlogsButton{
    margin: 40px auto;
}
.moreBlogsButton a{
    border: 1px solid black;
    text-align: center;
    padding: 5px 10px;
    border-radius: 4px;
}
.moreBlogsButton a:hover{
